// Animations
@keyframes cursor-animation {
  0%, 100% { cursor: url('../assets/cursors/frame-1.png'), auto; }
  12.5% { cursor: url('../assets/cursors/frame-2.png'), auto; }
  25% { cursor: url('../assets/cursors/frame-3.png'), auto; }
  37.5% { cursor: url('../assets/cursors/frame-4.png'), auto; }
  50% { cursor: url('../assets/cursors/frame-5.png'), auto; }
  62.5% { cursor: url('../assets/cursors/frame-6.png'), auto; }
  75% { cursor: url('../assets/cursors/frame-7.png'), auto; }
  87.5% { cursor: url('../assets/cursors/frame-8.png'), auto; }
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@keyframes rotation {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes loading-shine {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

@keyframes block-appear {
  0% {
    transform: scale(0);
    opacity: 0.2;
  }
  50% {
    transform: scale(1.2);
    opacity: 0.7;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}


@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate(-50%, 20px);
  }
  100% {
    opacity: 1;
    transform: translate(-50%, 0);
  }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes popIn {
    from {
        transform: scale(0);
    }
    to {
        transform: scale(1);
    }
}

@keyframes animate {
  0% {
    transform: translateY(0) rotate(0deg);
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}
